$montserrateFont: "Montserrat", open sans-serif;
$orangeColor: #F64D33;
$greenColor: #10B4A9;
$blackColor: #2E2E2E;

.flex{
    display:flex;
    align-items: center;
}

h1{
    color:$greenColor;
    font-weight: 600;
    font-size: 5em;
    line-height: 100%;
    margin-left: 100px;
}

.login{
    padding-top: 100px;    
    padding-bottom: 144px;

    &__tittle{
        font-weight: 600;
        color:$orangeColor;
        font-size: 1.5em;
    }
    
    &__psmall{
        font-size: 1em;
        color: $blackColor;
        font-weight: 400;
    }

    &__linkAccount{
        text-decoration: underline;
        margin-left: 5px;
    }

    &__container{
        border-radius: 0 0 25px 25px;
        box-shadow: 10px 10px 20px 2px #d6d6d6;  
        padding: 16px 45px 30px 45px;
        margin-left: 100px;
    }

    & form *, &__p{
        margin-bottom: 25px;
    }

    & input, &__link{
        border:solid $greenColor 1px;
        border-radius: 5px;
        font-family: $montserrateFont;
        font-weight: 300;
        font-size: 1.125em;
        padding: 14px 0 14px 10px;
        height: 40px;
        width: 442px;
    }

    &__input{
        color:$blackColor;
    }

    &__submit{
        background-color: $greenColor;
        color:white;
    }

    &__link{
        text-align: center;
    }

    &__p{
        text-align: center;
        color: $blackColor;
    }

    &__othersEmail{
        flex-direction: column;
    }
}

.logOut{
    display: none;
    justify-content: space-around;
    margin-top: 150px;
    margin-bottom: 150px;
    flex-direction: column;

    & h1{
        margin-left: 0;
        margin-bottom: 50px;
    }
    &__link{
        height: 40px;
        width: 442px;
        border: none;
        background-color: $greenColor;
        color: white;
        text-align: center;
        border-radius: 5px;
        padding: 14px 0 14px 10px;
        font-family: Montserrat, open sans-serif;
        font-size: 1.125em;
        font-weight: 300;
    }
}